@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

span {
  display: inline-block;
}

a {
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
}

img {
  max-width: 100%;
}

.header {
  padding: 7px 0;
  background-color: #db3534;
  box-shadow: 0 5px 5px 0 rgba(16, 16, 15, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 45px;
  z-index: 8;
}

.header .container {
  display: flex;
  justify-content: space-between;
}

.header .container .nav {
  display: inline-block;
  height: 33px;
  width: 33px;
  position: relative;
}

.header .container .nav__btn {
  display: flex;
  flex-flow: row wrap;
  align-content: space-around;
  height: 33px;
  width: 33px;
}

.header .container .nav__btn__line {
  display: block;
  height: 5px;
  width: 100%;
  background-color: #fff;
}

.header .container .nav__menu {
  display: none;
  background-color: black;
  opacity: 0.851;
  position: absolute;
  margin-top: 20px;
  padding: 10px 20px;
  width: 200px;
}

.header .container .nav__menu:before {
  content: '';
  display: inline-block;
  position: absolute;
  top: -15px;
  left: 10px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 15px solid #000;
  opacity: .851;
}

.header .container .nav__menu__link {
  margin: 10px 0;
  font-size: 14px;
  font-family: "HelveticaNeue", sans-serif;
  color: #fefefe;
  text-transform: uppercase;
  line-height: 1.2;
  transition: .3s ease;
}

.header .container .nav__menu__link:hover {
  color: #fe000a;
}

.header__right {
  display: inline-flex;
  justify-content: space-between;
  flex-flow: row wrap;
  align-items: center;
}

.header__right .cart {
  display: inline-flex;
  margin-right: 15px;
}

.header__right .cart__icon {
  display: flex;
  align-items: center;
  padding-right: 5px;
}

.header__right .cart__desc {
  padding-left: 5px;
  font-size: 12px;
  font-family: "HelveticaNeue", sans-serif;
  color: white;
  line-height: 1.2;
}

.header__right .lang {
  display: inline-flex;
  padding: 0 10px;
}

.header__right .lang__link {
  padding: 5px 7px;
  margin: 0 5px;
  background-color: rgba(0, 0, 0, 0.302);
  box-shadow: 0px 1px 1px 0px rgba(255, 255, 255, 0.5), inset 0px 2px 5px 0px rgba(0, 0, 0, 0.004);
  font-size: 18px;
  font-family: "HelveticaNeue", sans-serif;
  color: #ff3f3e;
  line-height: 1.2;
  transition: .3s ease-in-out;
}

.header__right .lang__link:hover {
  background-color: rgba(0, 0, 0, 0.17);
  box-shadow: 0px 1px 1px 0px rgba(255, 213, 205, 0.5), inset 0px 2px 5px 0px rgba(0, 0, 0, 0.004);
  color: #fff;
}

.header__right .lang .active {
  color: #fff;
}

.header__right .search {
  display: inline-flex;
  align-items: center;
  padding-left: 20px;
  font-family: "HelveticaNeue", sans-serif;
  position: relative;
}

.header__right .search__input {
  display: inline-block;
  width: 270px;
  height: 32px;
  padding: 7px;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.302);
  box-shadow: 0px 1px 1px 0px rgba(255, 255, 255, 0.5), inset 0px 2px 5px 0px rgba(0, 0, 0, 0.004);
  color: #fff;
}

.header__right .search__input::placeholder {
  color: #ff3f3e;
  text-indent: 0px;
  transition: text-indent .3s ease;
}

.header__right .search__input:focus::placeholder {
  text-indent: 500px;
  transition: text-indent 0.3s ease;
}

.header__right .search__btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 5px;
  background-color: #9a2922;
}

.sub-header {
  display: flex;
  justify-content: space-between;
  margin-top: 45px;
  padding: 30px 0;
}

.aside-left {
  flex: 1 15%;
